import React from "react"
import { Link } from "gatsby"
import Layout from "../components/Layout"
import styled from "styled-components"
import { motion } from "framer-motion"
import {
  Search as SearchIcon,
  Favorite as RelationshipIcon,
  Healing as BreakupIcon,
} from "@mui/icons-material"

import Seo from "../components/Seo"

const Container = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  top: -50px;
  min-height: 100vh;
  padding: 40px 20px;
  background: linear-gradient(to top, #f8f9fa, #e8eaf6);
  text-align: center;
`

const Title = styled(motion.h2)`
  font-size: 2.5rem;
  color: #31314d;
  margin-bottom: 50px;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.1);

  @media (max-width: 768px) {
    font-size: 2rem;
  }
`

const OptionsContainer = styled.div`
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 30px;
  width: 100%;
  max-width: 900px;
`

const StatusButton = styled(motion(Link))`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 40px 20px;
  background: white;
  color: #31314d;
  text-decoration: none;
  border-radius: 15px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.08);
  transition: all 0.3s ease;
  min-height: 250px;

  &:hover {
    transform: translateY(-5px);
    box-shadow: 0 15px 40px rgba(0, 0, 0, 0.12);
  }
`

const IconWrapper = styled.div`
  font-size: 60px;
  margin-bottom: 25px;
  color: #6c5ce7;
`

const ButtonText = styled.span`
  font-size: 1.5rem;
  font-weight: 600;
  margin-top: 15px;

  @media (max-width: 768px) {
    font-size: 1.3rem;
  }
`

const Description = styled.p`
  color: #666;
  margin-top: 15px;
  font-size: 1rem;
  line-height: 1.5;
`

const buttonVariants = {
  hidden: { opacity: 0, y: 20 },
  visible: {
    opacity: 1,
    y: 0,
    transition: {
      duration: 0.6,
      ease: "easeOut",
    },
  },
}

export default function Status() {
  return (
    <Layout>
      <Seo
        title="Выбор статуса отношений | Психологические тесты"
        description="Определите ваш статус: в активном поиске, в отношениях или после расставания. Персонализированные тесты и рекомендации."
      />
      <Container>
        <Title
          initial={{ opacity: 0, y: -20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6 }}
        >
          Укажите свой статус
        </Title>

        <OptionsContainer>
          <StatusButton
            to="/search"
            variants={buttonVariants}
            initial="hidden"
            animate="visible"
            transition={{ delay: 0.2 }}
            whileHover={{ scale: 1.03 }}
            whileTap={{ scale: 0.98 }}
          >
            <IconWrapper>
              <SearchIcon fontSize="inherit" />
            </IconWrapper>
            <ButtonText>Я в активном поиске</ButtonText>
            <Description>Найти подходящего партнера</Description>
          </StatusButton>

          <StatusButton
            to="/relationship"
            variants={buttonVariants}
            initial="hidden"
            animate="visible"
            transition={{ delay: 0.4 }}
            whileHover={{ scale: 1.03 }}
            whileTap={{ scale: 0.98 }}
          >
            <IconWrapper>
              <RelationshipIcon fontSize="inherit" />
            </IconWrapper>
            <ButtonText>Я в отношениях</ButtonText>
            <Description>Укрепить существующие отношения</Description>
          </StatusButton>

          <StatusButton
            to="/breakup"
            variants={buttonVariants}
            initial="hidden"
            animate="visible"
            transition={{ delay: 0.6 }}
            whileHover={{ scale: 1.03 }}
            whileTap={{ scale: 0.98 }}
          >
            <IconWrapper>
              <BreakupIcon fontSize="inherit" />
            </IconWrapper>
            <ButtonText>Развод/расставание</ButtonText>
            <Description>Помощь в сложный период</Description>
          </StatusButton>
        </OptionsContainer>
      </Container>
    </Layout>
  )
}
